import React from 'react';
import { HomepageBanner } from 'gatsby-theme-carbon';

// Component to be shadowed
import HomepageTemplate from 'gatsby-theme-carbon/src/templates/Homepage';

import HomepageBannerBackground from '../../images/background.png';

const BannerText = () => (
  <>
    <h1 className="banner-text-blurb">
      Maximo Autoscripting - Solving the world's problems one script at a time.
    </h1>
  </>
);

const customProps = {
  Banner: (
    <HomepageBanner renderText={BannerText} image={HomepageBannerBackground} />
  ),
  FirstCallout: <></>,
  SecondCallout: <></>,
};

// spreading the original props gives us props.children (mdx content)
function ShadowedHomepage(props) {
  return <HomepageTemplate {...props} {...customProps} />;
}

export default ShadowedHomepage;
